<template>
    <div id="app" ref="app">
        <div class="inputContainer" v-show="selectedInputField">
            <div class="filler"
                 @click="DeselectInputField()"></div>
            <textarea type="text"
                      class="webglInput"
                      ref="inputSim"
                      placeholder="Enter text..."
                      @input="SendInputed($event)" />
        </div>
        
        <button @click="SetFullscreen(isFullscreen? false: true)"
                ref="fullscreenBtn"
                id="fullscreenBtn">
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" class="bi bi-fullscreen" viewBox="0 0 16 16">
                <path d="M1.5 1a.5.5 0 0 0-.5.5v4a.5.5 0 0 1-1 0v-4A1.5 1.5 0 0 1 1.5 0h4a.5.5 0 0 1 0 1h-4zM10 .5a.5.5 0 0 1 .5-.5h4A1.5 1.5 0 0 1 16 1.5v4a.5.5 0 0 1-1 0v-4a.5.5 0 0 0-.5-.5h-4a.5.5 0 0 1-.5-.5zM.5 10a.5.5 0 0 1 .5.5v4a.5.5 0 0 0 .5.5h4a.5.5 0 0 1 0 1h-4A1.5 1.5 0 0 1 0 14.5v-4a.5.5 0 0 1 .5-.5zm15 0a.5.5 0 0 1 .5.5v4a1.5 1.5 0 0 1-1.5 1.5h-4a.5.5 0 0 1 0-1h4a.5.5 0 0 0 .5-.5v-4a.5.5 0 0 1 .5-.5z" />
            </svg>
        </button>
        <div id="container"
             class="fixed"
             ref="arContainer">
            <canvas style="display:block"
                    id="unity-canvas"
                    class="fixed"
                    ref="uc">
            </canvas>
        </div>
    </div>
</template>

<script>
    import { UnityController } from "@/js/UnityController";
    import { SD_Bridge } from "@/js/SD_Bridge";

    export default {
        name: 'App',
        components: {
        },
        data() {
            return {
                isFullscreen: false,
                selectedInputField: null,
                apiKey: "sk-DCpwGKpUQOZEixQAMPTzFizP4uCESH1xxlmAOV9mrViq2dVh",
                debugImage: ""
            };
        },
        mounted() {
            this.Initialization();
            window.GenerateImage = this.GenerateImage;
            window.OnInputFieldSelected = this.OnInputFieldSelected;
            window.OnInputFieldDeselected = this.OnInputFieldDeselected;
        },
        methods: {

            //InputFields
            OnInputFieldSelected(json) {
                let msg = JSON.parse(json);
                this.selectedInputField = msg.instanceId;

                let inputField = this.$refs.inputSim;
                inputField.value = msg.inputedText;
                inputField.focus
            },
            OnInputFieldDeselected() {
                this.selectedInputField = null;
            },
            DeselectInputField() {

                this.unityController.unityInstance.SendMessage("GenerationController", "DeselectInputField", this.selectedInputField.toString());
                this.OnInputFieldDeselected();
            },
            SendInputed(e) {
                this.unityController.unityInstance.SendMessage("GenerationController", "UpdateInputedData", e.target.value);
            },

            GenerateImage(parameters) {
                this.sdBridge.GenerateImage(this.unityController.unityInstance, JSON.parse(parameters));
            },
            async timeout(ms) {
                return new Promise(resolve => setTimeout(resolve, ms));
            },
            async Initialization() {
                let unityContainer = this.$refs.uc;

                this.unityController = new UnityController();
                this.sdBridge = new SD_Bridge(this.apiKey);

                this.unityController.Init(unityContainer, (unityInstance) => {
                    console.warn(unityInstance);
                });
            },

            SetFullscreen(state) {
                if (state) {
                    const lockScreen = () => {
                        this.isFullscreen = true;
                        if (window.matchMedia("(orientation: portrait)").matches) {
                            screen.orientation.lock("portrait-primary");
                        }
                        if (window.matchMedia("(orientation: landscape)").matches) {
                            screen.orientation.lock("landscape-primary");
                        }
                    }

                    if (this.$refs.app.requestFullscreen) {
                        this.$refs.app.requestFullscreen()
                            .then(lockScreen)
                            .catch((err) => {
                                console.error(err);
                            });
                    }

                    if (this.$refs.app.webkitRequestFullScreen) {
                        this.$refs.app.webkitRequestFullScreen()
                            .then(lockScreen)
                            .catch((err) => {
                                console.error(err);
                            });
                    }

                    if (this.$refs.app.mozRequestFullScreen) {
                        this.$refs.app.mozRequestFullScreen()
                            .then(lockScreen)
                            .catch((err) => {
                                console.error(err);
                            });
                    }

                } else {
                    document.exitFullscreen()
                        .then(() => {
                            this.isFullscreen = false;
                        })
                        .catch((err) => {
                            console.error(err);
                        })
                }
            },
        }
    }
</script>

<style>
    @import '../css/styles.css';

    .debuig {
        position:fixed;
        z-index: 99;
    }


    .debuig:hover {
        background-color: antiquewhite;
    }

    .inputContainer {
        z-index: 5;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 5px;
        display:flex;
        flex-direction:column;
        justify-content:flex-end;
    }

    .filler {
        flex: 1 1 auto;
    }

    .webglInput {
        height: 80px;
        width: 100%;
    }
</style>
